<template>
    <div class="person-center">
        <div class="block">
            <div v-if="!user.isLogin" class="banner">
                <img :src="appConfig.user.banner" alt>
                <button @click="openWin('/login', '登录')" :style="[appConfig.mainColor]">立即登录</button>
            </div>
            <div v-else class="banner logined">
                <img :src="appConfig.user.banner_user" alt>
                <span class="user">{{user.phone}}</span>
            </div>
            <div class="nav">
                <div class="flex text-center">
                    <div class="flex-1" @click="switchTab(2)">
                        <img :src="appConfig.user.recent" alt>
                        <div>近期待还</div>
                    </div>
                    <div class="flex-1" @click="switchTab(2)">
                        <img :src="appConfig.user.record" alt>
                        <div>借还记录</div>
                    </div>
                    <div class="flex-1" @click="switchTab(2)">
                        <img :src="appConfig.user.myOrder" alt>
                        <div>我的订单</div>
                    </div>
                </div>
            </div>
        </div>

        <div class="block">
            <div
                class="cell"
                v-for="(item, index) in vouchers"
                :key="index"
                @click="cellClick(item)"
                :class="{cancel: index == 1}"
            >
                <div class="cell-content flex">
                    <div class="left flex">
                        <img :src="item.icon" alt>
                        <div class="title">{{item.title}}</div>
                    </div>
                    <div class="right flex">
                        <div class="content">{{item.content}}</div>
                        <img
                            v-if="item.hasArrow"
                            src="https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181129/wode-jiantou%402x.png"
                        >
                    </div>
                </div>
            </div>
        </div>
        <div v-if="user.isLogin" class="block quit" @click="quit">安全退出</div>
    </div>
</template>
<script>
import { mapState } from 'vuex'
import { brigeNativeByJavascript } from '../api/Bridge.js'

const imgBaseUrl = 'https://money-arrive-static.oss-cn-shanghai-finance-1-pub.aliyuncs.com/20181129/icon'
export default {
    data() {
        return {
            isLogin: false,
            userPhone: '177****8051',
            vouchers: [
                {
                    icon: imgBaseUrl + '1%402x.png',
                    title: '我的优惠券',
                    hasArrow: true
                },
                {
                    icon: imgBaseUrl + '2%402x.png',
                    title: '息费抵扣金',
                    content: '0.00元',
                    hasArrow: true
                },
                {
                    icon: imgBaseUrl + '1%402x.png',
                    title: '微信公众号',
                    content: 'liegejinron',
                    hasArrow: false
                },
                {
                    icon: imgBaseUrl + '4%402x.png',
                    title: '我的客服',
                    hasArrow: true
                },
                {
                    icon: imgBaseUrl + '5%402x.png',
                    title: '关于我们',
                    hasArrow: true
                }
            ]
        }
    },
    methods: {
        cellClick(item) {
            if (item.title == '我的优惠券' || item.title == '息费抵扣金') {
                if (!this.user.isLogin) {
                    this.openWin('/login', '登录')
                    return
                }
                this.openWin('/myCoupon', '我的优惠券')
            }
            if (item.title == '关于我们') {
                this.openWin('/about', '关于我们')
            } else if (item.title == '我的客服') {
                this.openWin('/qiyu?ref=center', '在线客服')
            }
        },
        switchTab(tab) {
            if (!this.user.isLogin) {
                this.openWin('/login', '登录')
                return
            }
            brigeNativeByJavascript('toSwitchTab', { tab })
        },
        quit() {
            this.$store.commit('loading', true)
            setTimeout(() => {
                this.$store.commit('setUser', { isLogin: false })
                this.$store.commit('loading', false)
            }, 1000)
        }
    },
    computed: {
        ...mapState(['appConfig', 'user'])
    }
}
</script>

